export default "import React from 'react';\r\nimport {FormControl, InputGroup, Form} from 'react-bootstrap';\r\nimport styles from './index.module.scss';\r\n\r\nconst InputGroupDemo = () => {\r\n  return (\r\n    <div style={{width: '100%'}}>\r\n      <InputGroup className='mb-3'>\r\n        <InputGroup.Text\r\n          id='basic-addon1'\r\n          className={styles.inputGroupBtnRight}>\r\n          @\r\n        </InputGroup.Text>\r\n        <FormControl\r\n          placeholder='Username'\r\n          aria-label='Username'\r\n          aria-describedby='basic-addon1'\r\n          className={styles.inputFormControlLeft}\r\n        />\r\n      </InputGroup>\r\n\r\n      <InputGroup className='mb-3'>\r\n        <FormControl\r\n          placeholder=\"Recipient's username\"\r\n          aria-label=\"Recipient's username\"\r\n          aria-describedby='basic-addon2'\r\n          className={styles.inputFormControlRight}\r\n        />\r\n        <InputGroup.Text id='basic-addon2' className={styles.inputGroupBtnLeft}>\r\n          @example.com\r\n        </InputGroup.Text>\r\n      </InputGroup>\r\n\r\n      <Form.Label htmlFor='basic-url'>Your vanity URL</Form.Label>\r\n      <InputGroup className='mb-3'>\r\n        <InputGroup.Text\r\n          id='basic-addon3'\r\n          className={styles.inputGroupBtnRight}>\r\n          https://example.com/users/\r\n        </InputGroup.Text>\r\n        <FormControl\r\n          id='basic-url'\r\n          aria-describedby='basic-addon3'\r\n          className={styles.inputFormControlLeft}\r\n        />\r\n      </InputGroup>\r\n\r\n      <InputGroup className='mb-3'>\r\n        <InputGroup.Text className={styles.inputGroupBtnRight}>\r\n          $\r\n        </InputGroup.Text>\r\n        <FormControl aria-label='Amount (to the nearest dollar)' />\r\n        <InputGroup.Text className={styles.inputGroupBtnLeft}>\r\n          .00\r\n        </InputGroup.Text>\r\n      </InputGroup>\r\n\r\n      <InputGroup>\r\n        <InputGroup.Text className={styles.inputGroupBtnRight}>\r\n          With textarea\r\n        </InputGroup.Text>\r\n        <FormControl\r\n          as='textarea'\r\n          aria-label='With textarea'\r\n          className={styles.inputFormControlLeft}\r\n        />\r\n      </InputGroup>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default InputGroupDemo;\r\n";