export default "import clsx from 'clsx';\r\nimport React from 'react';\r\nimport {\r\n  DropdownButton,\r\n  FormControl,\r\n  Dropdown,\r\n  InputGroup,\r\n} from 'react-bootstrap';\r\nimport styles from './index.module.scss';\r\n\r\nconst DropdownsDemo = () => {\r\n  return (\r\n    <div>\r\n      <>\r\n        <InputGroup className={clsx(styles.dropDownDemoWrapper1, 'mb-3')}>\r\n          <DropdownButton\r\n            variant='outline-secondary'\r\n            title='Dropdown'\r\n            id='input-group-dropdown-1'>\r\n            <Dropdown.Item href='#'>Action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Another action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Something else here</Dropdown.Item>\r\n            <Dropdown.Divider />\r\n            <Dropdown.Item href='#'>Separated link</Dropdown.Item>\r\n          </DropdownButton>\r\n          <FormControl\r\n            aria-label='Text input with dropdown button'\r\n            className={styles.inputFormControlLeft}\r\n          />\r\n        </InputGroup>\r\n\r\n        <InputGroup className={clsx(styles.dropDownDemoWrapper2, 'mb-3')}>\r\n          <FormControl\r\n            aria-label='Text input with dropdown button'\r\n            className={styles.inputFormControlRight}\r\n          />\r\n\r\n          <DropdownButton\r\n            variant='outline-secondary'\r\n            title='Dropdown'\r\n            id='input-group-dropdown-2'\r\n            align='end'>\r\n            <Dropdown.Item href='#'>Action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Another action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Something else here</Dropdown.Item>\r\n            <Dropdown.Divider />\r\n            <Dropdown.Item href='#'>Separated link</Dropdown.Item>\r\n          </DropdownButton>\r\n        </InputGroup>\r\n\r\n        <InputGroup className={clsx(styles.dropDownDemoWrapper3, 'mb-3')}>\r\n          <DropdownButton\r\n            variant='outline-secondary'\r\n            title='Dropdown'\r\n            id='input-group-dropdown-3'>\r\n            <Dropdown.Item href='#'>Action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Another action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Something else here</Dropdown.Item>\r\n            <Dropdown.Divider />\r\n            <Dropdown.Item href='#'>Separated link</Dropdown.Item>\r\n          </DropdownButton>\r\n          <FormControl aria-label='Text input with 2 dropdown buttons' />\r\n          <DropdownButton\r\n            variant='outline-secondary'\r\n            title='Dropdown'\r\n            id='input-group-dropdown-4'\r\n            align='end'>\r\n            <Dropdown.Item href='#'>Action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Another action</Dropdown.Item>\r\n            <Dropdown.Item href='#'>Something else here</Dropdown.Item>\r\n            <Dropdown.Divider />\r\n            <Dropdown.Item href='#'>Separated link</Dropdown.Item>\r\n          </DropdownButton>\r\n        </InputGroup>\r\n      </>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default DropdownsDemo;\r\n";